	import {  
	    mapState,  
	    mapMutations  
	} from 'vuex';  
	export default {
		data() {
			return {
				
			};
		},
		computed:{
			...mapState(['unifiedUser']),
		},
		methods: {
			...mapMutations(['login']),
 			imgMemberPath:function(path){
				if(path==''||path==null||path=='null'){
					return "/static/missing-face.png"				 
				}else if (path!=''&&path.indexOf('http')>=0) {
					return path;				 
				}
				return this.siteHost + path;
			},
			//提交
			confirm(){ 
				let _this=this;
				this.sendRequest({
					url : 'save_profile',
					data : this.unifiedUser,
					method : "post",
					success:function (res) {
						if(res.code=='200'){
							_this.$api.msg(res.message);
 							_this.login({unifiedUser:res.data});
							uni.navigateBack();  
						}else{
							_this.$api.msg(res.message);
						}
					}
				}); 
			},
			uploadImg:function(){
				var _this=this;
				uni.chooseImage({
					success: function (res) {
						const tempFilePaths = res.tempFilePaths;
						_this.sendUpload({
							url : 'image_Up',
							filePath: tempFilePaths[0],
							name: 'file',
							success:function (res) {
								if(res.code=='200'){
									_this.$api.msg(res.message);
									_this.unifiedUser.avatar=res.data.fileUrl;
								}else{
									_this.$api.msg(res.message);
								}
							}
						});  
					},fail:function(res){
						console.log("用户取消上传文件")
					}
				});
			},
		}
	}
